.why {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.why__reasons {
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: 100%;
    max-width: 900px;
}

.reason {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(92.32deg, rgba(137, 129, 222, 0.12) 0%, rgba(240, 149, 202, 0.12) 98.07%);
    border-radius: 32px;
    padding: 16px 32px;
}

.reason__title {
    font-family: 'Mulish', sans-serif;
    font-style: normal;
    font-weight: 900;
    font-size: 25px;
    line-height: 36px;
    margin: 0;
}

.reason__text {
    font-family: 'Mulish', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    max-width: 545px;
    width: 100%;
    color: #333333;
    margin: 0;
}

@media (max-width: 800px) {
    .reason {
        flex-direction: column;
        align-items: flex-start;
        padding: 16px;
    }

    .reason__title {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 8px;
    }
}
