@font-face {
    font-family: 'Mulish';
    src: url('../assets/fonts/Mulish.ttf') format('truetype');
}

@font-face {
    font-family: 'Mulish-Italic';
    src: url('../assets/fonts/Mulish.ttf') format('truetype');
}

* {
    outline: none;
}

body {
    margin: 0;
    font-family: 'Mulish', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-style: normal;
    font-weight: 600;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

button {
    cursor: pointer;
    white-space: nowrap;
}

.loading {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    width: 100%;
    height: 100%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

.gradient-text {
    background: linear-gradient(92.32deg, #2E22AC 0%, #DB499E 98.07%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.link {
    background: linear-gradient(92.32deg, #2E22AC 0%, #DB499E 98.07%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-decoration: none;

    font-family: 'Mulish', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 23px;
    line-height: 56px;
}

.link:hover {
    background: linear-gradient(92.32deg, #DB499E 0%, #2E22AC 98.07%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: .5s;
}

.title {
    font-family: 'Mulish', sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 40px;
    line-height: 56px;
    background: linear-gradient(92.32deg, #2E22AC 0%, #DB499E 98.07%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 30px 0;
}

.title_white {
    font-family: 'Mulish', sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 40px;
    line-height: 56px;
    color: #FFFFFF;
    display: flex;
    /*border-bottom: 1px solid #F3F3F3;*/
}


.connect_wallet {
    background-image: linear-gradient(92.32deg, #2e22ac 0%, #db499e 98.07%);
    padding: 2px;
    border-radius: 10px;
    box-sizing: border-box;
}

.connect_wallet_button {
    background-image: linear-gradient(92.32deg, #2e22ac 0%, #db499e 98.07%);
    cursor: pointer;
    border: none;
    padding: 0;
    border-radius: 8px;
    width: 100%;
    margin: 0;
}

.connect_wallet p {
    padding: 8px 25px;
    font-size: 28px;
    line-height: 38px;
    margin: 0;
    color: #fff;
    font-weight: 600;
}

table {
    width: 100%
}

table,
th,
td {
    border: 1px solid transparent;
    border-collapse: collapse
}

th {
    text-align: left
}

.loading {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center
}

.link {
    background: linear-gradient(92.32deg, #2E22AC 0%, #DB499E 98.07%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-decoration: none;
    font-family: "Mulish", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 23px;
    line-height: 56px
}

.link:hover {
    background: linear-gradient(92.32deg, #DB499E 0%, #2E22AC 98.07%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    transition: .5s
}

.walletconnect-qrcode__image {
    width: calc(80% - 30px) !important
}

.w-100 {
    width: 100%;
}


.subtitle {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    max-width: 750px
}

.accent {
    font-weight: 800;
    font-size: 16px;
    line-height: 24px;
    color: #2e22ac
}

.separator {
    width: 100%;
    display: flex;
    height: 2px;
    background: #d9d9d9;
    margin: 20px 0
}

.btn_link {
    background: transparent;
    border: 1.5px solid transparent;
    border-radius: 8px;
    padding: 6px 16px;
    font-family: "Mulish", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #0084e4;
    margin-right: 16px
}

.btn_link:hover {
    background: transparent;
    border: 1.5px solid #0084e4
}

.mobile-only {
    display: none
}

.desktop-only {
    display: flex
}

.alignLeft {
    text-align: left !important;
}

@media(max-width: 1200px) {
    .container {
        width: 90%;
        height: 100%
    }
}

@media(max-width: 800px) {
    .mobile-only {
        display: flex
    }

    .desktop-only {
        display: none
    }

    .title {
        font-size: 26px;
        line-height: 32px;
        text-align: left;
        max-width: 100%;
        margin: 20px 0;
        text-align: center
    }

    .main>.main__row>.column>.title {
        text-align: left;
        margin: 0
    }

    .title_white {
        font-size: 28px;
        line-height: 32px;
        max-width: 100%;
        margin: 0;
        padding-bottom: 10px
    }

    .subtitle {
        font-weight: 400;
        font-size: 18px;
        line-height: 30px;
        text-align: left;
        max-width: 100%
    }

    button {
        width: 100%
    }

    .btn_filled {
        margin-right: 0
    }

    .btn_filled_white {
        margin-right: 0
    }

    .btn_outlined {
        margin-right: 0
    }

    .btn_link {
        margin-right: 0
    }
}

