.details {
    width: 100%;
    box-sizing: border-box;
    border: 3px solid #CF47A0;
    border-radius: 16px;
    padding: 10px 24px;
    margin-bottom: 16px;
}

.details__header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}

.summary {
    font-weight: 700;
    font-size: 20px;
    line-height: 32px;
    margin: 0;
}

.details__text {
    font-weight: 400;
    width: 100%;
    font-size: 16px;
    line-height: 24px;
    color: #333333;
    margin: 0;
    transition: max-height 0.5s;
    max-height: 0;
    overflow: hidden;
}


.details__text_open {
    transition: max-height 1s;
    max-height: 500px;
}

@media (max-width: 800px) {
    .summary {
        font-size: 18px;
        line-height: 23px;
        margin: 0;
    }

    .details__text {
        font-size: 16px;
        line-height: 24px;
        margin: 0;
    }
}
