.how {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 50px;
}

.how__wrapper {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 16px;
}

.how__step {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 25%;
}

.how__step__title {
    font-family: 'Mulish', sans-serif;
    font-style: normal;
    font-weight: 800;
    font-size: 30px;
    line-height: 56px;
}

.how__step__img {
    align-self: center;
    height: 195px;
    /*height: auto;*/
    /*width: 100%;*/
    width: auto;
}

.how__step__img_3 {
    align-self: center;
    /*height: 195px;*/
    height: auto;
    width: 80%;
    /*width: auto;*/
}

.how__step__text {
    font-family: 'Mulish', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 32px;
    color: #333333;
}

@media (max-width: 800px) {
    .how {
        width: 100%;
    }

    .how__wrapper {
        display: flex;
        flex-direction: column;
    }

    .how__step {
        width: 100%;
        align-items: center;
        text-align: center;
    }

    .how__step__title {
        margin: 10px;
        font-size: 22px;
        line-height: 24px;
    }

    .how__step__text {
        font-size: 17px;
        line-height: 24px;
    }

}
