footer {
    width: 100%;
    /*height: 100px;*/
    background: #333333;
    margin-top: 30px;
    padding: 20px 0;
}

.footer__container {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 10px;
}

.footer__links {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: center;
    gap: 32px;
}

.footer__link {
    font-family: 'Mulish', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 24px;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.4);
    letter-spacing: 1px;
}

.footer__link:hover {
    color: #FFFFFF;
}

.copyright {
    font-family: 'Mulish', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    margin: 0px;
    color: rgba(255, 255, 255, 0.4);
}

@media (max-width: 800px) {
    footer {
        /*height: 150px;*/
        padding: 20px 0;
    }

    .footer__container {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .copyright {
        margin: 0;
        text-align: center;
        margin-top: 10px;
    }

    .footer__links {
        display: flex;
        flex-direction: column;
        width: 100%;
        align-items: center;
        justify-content: space-between;
        gap: 15px;
    }
}
